body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

a {
  text-decoration: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  background-color: #6b6b6b;
}

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.itemBoxItem {
  display: flex;
  flex-wrap: wrap;
  margin-left: -16px;
  margin-right: -16px;
  justify-content: center;
  background-color: #b1b1b1;
}

.imgBox {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 16px;
  margin-right: 16px;
  width: calc(25% - 32px);
  margin-top: 25px;
  margin-bottom: 10px;
  position: relative;
  overflow: hidden;
}

.imgBox a {
  display: block;
}

.imgBox a i {
  position: absolute;
  font-size: 30px;
  color: #fff;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
  width: 60px;
  height: 60px;
  margin: auto;
  text-align: center;
  line-height: 60px;
  transform: scale(3);
  background: rgba(255, 255, 255, 0.3);
  box-shadow: 0 0 0 8px rgba(180, 180, 180, 0.3);
  border-radius: 100%;
  opacity: 0;
  transition: all .5s;
  z-index: 2;
}

.imgBox a img {
  transition: all 0.5s;
}

.imgBox:hover a i {
  opacity: 1;
  transform: scale(1);
}

.imgBox:hover a img {
  transform: scale(1.2);
}

.imgBox a::before {
  position: absolute;
  content: '';
  background: rgba(0, 0, 0, 0.4);
  width: 0;
  height: 100%;
  left: 50%;
  transition: all 0.5s;
  z-index: 1;
}

.imgBox a:hover::before {
  width: 100%;
  left: 0;
}

.imgBox:hover .textBox {
  transform: translateY(150%);
}

.textBox {
  position: absolute;
  bottom: 5%;
  color: #ffffff;
  background: rgba(0, 0, 0, 0.3);
  text-align: center;
  width: 100%;
  text-transform: uppercase;
  transition: all 0.5s;
}

.textBox h4 {
  font-size: 20px;
  font-weight: bold;
  margin: 6px 0;
}

.textBox span {
  border-top: 1px solid #fff;
  border-bottom: 1px solid #fff;
  padding: 2px 0 2px;
}

.textBox p {
  text-transform: none;
  font-size: 15px;
  margin: 10px 10px 5px;
}

.Dva {
  display: flex;
  flex-wrap: wrap;
  margin-left: -16px;
  margin-right: -16px;
}

.BoxSponsor {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 16px;
  margin-right: 16px;
  width: calc(16.66667% - 32px);
  margin-top: 25px;
  margin-bottom: 10px;
  position: relative;
}

.BoxSponsor img {
  border-radius: 100%;
}

h2,
.h2 {
  font-size: 2.5rem;
  text-align: center;
  margin: 20px 0;
}

.h2sub {
  text-align: center;
  font-size: 20px;
  margin: 20px 0;
}

.profit {
  background: #000;
  padding: 15px 0;
}

.profit h2 {
  color: #fff;
}

.profit .h2sub {
  color: #642;
}

.profit .itemsi {
  display: flex;
  flex-wrap: wrap;
  margin-left: -16px;
  margin-right: -16px;
  justify-content: center;
}

.profit .itemsi .itemi {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 16px;
  margin-right: 16px;
  width: calc(33.33333% - 32px);
  text-align: center;
  margin-top: 15px;
  margin-bottom: 15px;
}

.profit .itemsi .itemi .img {
  border-radius: 50%;
}

.profit .itemsi .itemi .subtitle {
  color: #fff;
  font-size: 1.5rem;
  margin: 3px 0;
}

.profit .itemsi .itemi .text {
  font-size: 16px;
  color: #d94948;
}

@media screen and (max-width: 1200px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 960px) {
  .imgBox {
    width: calc(41.66667% - 32px);
  }

  .BoxSponsor {
    width: calc(16.66667% - 32px);
  }

  .profit .itemsi .itemi {
    width: calc(50% - 32px);
  }
}

@media screen and (max-width: 780px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .BoxSponsor {
    width: calc(16.66667% - 32px);
  }

  .profit .itemsi .itemi {
    width: calc(66.66667% - 32px);
  }
}

@media screen and (max-width: 560px) {
  .imgBox {
    width: calc(83.33333% - 32px);
  }

  .BoxSponsor {
    display: none;
  }

  .profit .itemsi .itemi {
    width: calc(100% - 32px);
  }
}