@import "smart-grid.sass";
// @import "fontawesome"
// @import url('https://fonts.googleapis.com/css?family=Montserrat:500|Nunito+Sans:400,700|Work+Sans')

@include reset();
body {
    background-color: #6b6b6b; }
.wrapper {
    @include wrapper(); }
// .debug
//     +debug (rgba(0, 0, 0, 0.5), 1px solid #ff0)
.itemBoxItem {
    @include row-flex();
    justify-content: center;
    background-color: #b1b1b1; }
.imgBox {
    @include col();
    @include size(3);
    @include size-md(5);
    @include size-xs(10);
    margin-top: 25px;
    margin-bottom: 10px;
    position: relative;
    overflow: hidden;
    a {
        display: block;
        i {
            position: absolute;
            font-size: 30px;
            color: #fff;
            right: 0;
            left: 0;
            top: 0;
            bottom: 0;
            width: 60px;
            height: 60px;
            margin: auto;
            text-align: center;
            line-height: 60px;
            transform: scale(3);
            background: rgba(255, 255, 255, 0.3);
            box-shadow: 0 0 0 8px rgba(180, 180, 180, 0.3);
            border-radius: 100%;
            opacity: 0;
            transition: all .5s;
            z-index: 2; }
        img {
            transition: all 0.5s; } }
    &:hover a i {
                opacity: 1;
                transform: scale(1); }
    &:hover a img {
                transform: scale(1.2); }
    a {
        &::before {
            position: absolute;
            content: '';
            background: rgba(0,0,0, 0.4);
            width: 0;
            height: 100%;
            left: 50%;
            transition: all 0.5s;
            z-index: 1; }
        &:hover {
            &::before {
                width: 100%;
                left: 0; } } }
    &:hover {
        .textBox {
            transform: translateY(150%); } } }
.textBox {
 position: absolute;
 bottom: 5%;
 color: #ffffff;
 background: rgba(0,0,0, 0.3);
 text-align: center;
 width: 100%;
 text-transform: uppercase;
 transition: all 0.5s; }

.textBox h4 {
 font-size: 20px;
 font-weight: bold;
 margin: 6px 0; }

.textBox span {
 border-top: 1px solid #fff;
 border-bottom: 1px solid #fff;
 padding: 2px 0 2px; }

.textBox p {
 text-transform: none;
 font-size: 15px;
 margin: 10px 10px 5px; }

.Dva {
    @include row-flex(); }
.BoxSponsor {
    @include col();
    @include size(2);
    @include size-md(2);
    @include size-sm(2);
    @include xs(display, none);
    margin-top: 25px;
    margin-bottom: 10px;
    position: relative;
    img {
        border-radius: 100%; } }

h2, .h2 {
    font-size: 2.5rem;
    text-align: center;
    margin: 20px 0; }


.h2sub {
    text-align: center;
    font-size: 20px;
    margin: 20px 0; }

.profit {
    background: #000;
    padding: 15px 0;
    h2 {
        color: #fff; }
    .h2sub {
        color: #642; }
    .itemsi {
        @include row-flex();
        justify-content: center;

        .itemi {
            @include col();
            @include size(4);
            @include size-md(6);
            @include size-sm(8);
            @include size-xs(12);
            text-align: center;
            margin-top: 15px;
            margin-bottom: 15px;

            .img {
                border-radius: 50%; }


            .subtitle {
                color: #fff;
                font-size: 1.5rem;
                margin: 3px 0; }

            .text {
                font-size: 16px;
                color: #d94948; } } } }
